$offset1: 10px;    $offset1r: 10px;
$offset2: 10px;    $offset2r: 14px;
$offset3: 16px;    $offset3r: 20px;
$offset4: 24px;    $offset4r: 28px;
$offset5: 32px;    $offset5r: 40px;
$offset6: 48px;    $offset6r: 56px;
$offset7: 64px;    $offset6r: 72px;

$fontsize1: 14px;  $fontsize1r: 14px;
$fontsize2: 14px;  $fontsize2r: 14px;
$fontsize3: 16px;  $fontsize3r: 18px;
$fontsize4: 20px;  $fontsize4r: 22px;
$fontsize5: 24px;  $fontsize5r: 28px;
$fontsize6: 32px;  $fontsize6r: 40px;

// $smaller: 430px;
$smaller: 770px;

h6{ font-size: $fontsize1 };
h5{ font-size: $fontsize2 };
h4{ font-size: $fontsize3 };
h3{ font-size: $fontsize4 };
h2{ font-size: $fontsize5 };
h1{ font-size: $fontsize6 };


h1, h2, h3, h4, h5, h6{
    margin: 0;
    font-weight: normal;
}

@mixin flex($justify: center, $align: center, $direction: row){
    display: flex;
    justify-content: $justify;
    align-items: $align;
    flex-direction: $direction;
}

@mixin rect($res: $offset6){
    width: $res;
    height: $res;
    @include flex;
}

@mixin circle($rad: 100vmax){
    border-radius: $rad;
}

@mixin center {
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

@mixin defaultTransition(){
    transition-duration: var(--defaultTransitionTime);
}









